import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';

import { BrowserRouter } from 'react-router-dom'
import {Provider} from 'react-redux'
import { createStore } from 'redux';
import { combineReducers } from 'redux';


let basicState = [{id:0, name:'', quan: 2}, {id:1, name:'2', quan: 1} ];

let showsAlert = true;

//reduver   state  .
function reducer(state = basicState, action){
  
  //reducer state    .
  if(action.type=== ''){

    let copy = [...state];
    
    if(copy.some(c=>c.id === action.payload.id)){
      copy.find(c=>c.id === action.payload.id).quan++;
    }else{

      copy.push(action.payload);
    }


    console.log(copy)

    return copy;

  }else if(action.type === ''){

    console.log(action.payload);

    let copy = [...state];

    copy[action.payload.id].quan++;

    return copy;

  } else if(action.type === '') {

    let copy = [...state];

    copy[action.payload.id].quan--;

    return copy;

  } else {
    return state;
  }
}




function reducer2(state = showsAlert, action){
  
  if(action.type === ''){
    showsAlert = !showsAlert;

    return showsAlert;
  } else{
    return showsAlert;
  }

}

let store = createStore(combineReducers({ reducer, reducer2 }));


ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();

